import React, { Component } from "react";

import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import { Time } from "../../modules/Time";
import { Population } from "../../modules/Population";
import { getCityName } from "../../utils/path";
import Page from "../../ui/Page";

class CityDash extends React.Component {
  state = {
    name: ""
  };

  render() {
    const { classes, cities } = this.props;
    return (
      <Page name={getCityName(this.props.location.pathname)}>
        <Time />
        <br />
        <Population />
      </Page>
    );
  }
}

export default CityDash;



