import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import AddIcon from "@material-ui/icons/Add";
import { City } from "../../modules/City";
import { Link } from "react-router-dom";
import Page from "../../ui/Page";
import Card from "../../ui/Card";

const styles = {
  item: {
    display: "flex",
    justifyContent: "center"
  },
  button: {
    position: "fixed",
    bottom: "35px",
    right: "35px"
  }
};

class World extends Component {
  generateCityCards = cities => {
    if (cities) {
      return Object.keys(cities).map((key, i) => {
        let o = cities[key];
        return (
          <Card style={styles.item} key={i}>
            <City data={o} />
          </Card>
        );
      });
    }
  };

  render() {
    return (
      <Page>
        {this.generateCityCards(this.props.cities)}
        <Link to="/add">
          <Button
            variant="fab"
            mini={true}
            color="primary"
            aria-label="add"
            style={styles.button}
          >
            <AddIcon />
          </Button>
        </Link>
      </Page>
    );
  }
}

export default World;



