import React, { Component } from "react";
import "./App.css";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { World } from "./components/World";
import { AddCity } from "./components/AddCity";
import { CityDash } from "./components/CityDash";
import { Provider } from "react-redux";
import { store } from "./Store";
import styled from "styled-components";

import { HashRouter as Router, Route, Link, Switch } from "react-router-dom";

import { PersistGate } from "redux-persist/integration/react";

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <AppBar position="sticky" color="default">
              <br />
              <Toolbar>
                <Typography variant="display2" gutterBottom gutterTop>
                  <StyledLink to="/citysim">City Sim</StyledLink>
                </Typography>
              </Toolbar>
            </AppBar>
            <Switch>
              <Route path="/citydash" component={CityDash} />
              <Route path="/add" component={AddCity} />
              <Route path="/" component={World} />
            </Switch>
          </div>
        </Router>
      </Provider>
    );
  }
}

const StyledLink = styled(Link)`
  text-decoration: none;
  margin-top: 20px;
`;

export default App;



