import React, { Component } from "react";

import Card from "../../ui/Card";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import PlayIcon from "@material-ui/icons/PlayCircleFilled";
import PauseIcon from "@material-ui/icons/PauseCircleFilled";
import { withRouter } from "react-router";
import { getCityName } from "utils/path";
import styled from "styled-components";

class Population extends Component {
  state = {
    interval: undefined
  };

  componentDidMount() {
    this.setState({ interval: setInterval(this.tickTock, 1000) });
  }

  handlePause = () => {
    clearInterval(this.state.interval);
  };

  handlePlay = () => {
    this.setState({ interval: setInterval(this.tickTock, 1000) });
  };

  tickTock = () => {
    this.props.dispatchTickTime(getCityName(this.props.location.pathname));
  };

  render() {
    return (
      <Card name="Population">
        <br />
        <br />
        <ButtonBar>
          <Button size="large" color="primary" onClick={this.handlePlay}>
            <PlayIcon />
          </Button>
          <Button size="large" color="primary" onClick={this.handlePause}>
            <PauseIcon />
          </Button>
        </ButtonBar>
      </Card>
    );
  }
}

const ButtonBar = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
`;

export default withRouter(Population);



